body{
    background-color: #f5d9df;
    font-family: "Nunito",sans-serif;
    margin: 0;
    padding: 0;
    font-family: Nunito;
    box-sizing: border-box;
  }
  .blurb h2{
    color:#EA1C2C;
    font-weight:100;
    font-size:2.5rem;
  }
  .blurb p{
    color:#f498b8;
    font-weight:100;
    font-size:1.125rem;
    line-height: 2;
  }
  .content{
    margin-top:100px;
    margin-bottom: 100px;
  
  }
  
  #mainNavbar{
    font-size: 1.5rem;position: static;
    font-weight:100;
    background-color: rgba(255, 192, 203, 0.476)
  }
  
  #mainNavbar .nav-link{
    color:white;
  }
  #mainNavbar .nav-link:hover {
    color:#EA1C2C;
  }
  
  
  #mainNavbar .navbar-brand{
    color:#EA1C2C;
    font-size:1.5rem;
  
  }
  #headinggrp span{
    color:#EA1C2C;
  }
  #headinggrp h1{
    font-weight: 100;
    font-size: 4rem;
  
  }
  .navbar.scrolled{
    background: rgb(222,192,222);
    transition: background 1s;
  }
  @media (max-width:1200px){
    #headinggrp h1{
      font-weight: 100;
      font-size: 3rem;
    
    }
    
  }  